.container {
    display: flex;
    flex-direction: column;
    gap: var(--space-3);
    padding: var(--space-4);
    background: var(--bg-card);
    border: 1px solid var(--border-subtle);
    border-radius: var(--radius-xl);
}

/* Main */
.main {
    display: flex;
    align-items: center;
    gap: var(--space-3);
}

.fireContainer {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.fireIcon {
    width: 32px;
    height: 32px;
    color: var(--text-muted);
    transition: all var(--transition-normal);
}

.fireIcon.active {
    color: #f59e0b;
    filter: drop-shadow(0 0 8px rgba(245, 158, 11, 0.6));
}

.fireGlow {
    position: absolute;
    inset: -8px;
    background: radial-gradient(circle, rgba(245, 158, 11, 0.3) 0%, transparent 70%);
    animation: fireGlow 1.5s ease-in-out infinite;
}

@keyframes fireGlow {

    0%,
    100% {
        opacity: 0.5;
        transform: scale(1);
    }

    50% {
        opacity: 1;
        transform: scale(1.2);
    }
}

.streakInfo {
    display: flex;
    flex-direction: column;
}

.label {
    font-size: 0.75rem;
    font-weight: 500;
    color: var(--text-muted);
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.count {
    font-size: 1.5rem;
    font-weight: 800;
    font-family: var(--font-display);
    background: linear-gradient(135deg, #f59e0b, #ef4444);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    line-height: 1.2;
}

/* Stats */
.stats {
    display: flex;
    gap: var(--space-4);
    padding-top: var(--space-3);
    border-top: 1px solid var(--border-subtle);
}

.stat {
    display: flex;
    align-items: center;
    gap: var(--space-2);
    font-size: 0.8125rem;
    color: var(--text-muted);
}

.stat svg {
    width: 14px;
    height: 14px;
}

/* Check In Button */
.checkInButton {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--space-2);
    padding: var(--space-3);
    font-size: 0.875rem;
    font-weight: 600;
    border-radius: var(--radius-lg);
    transition: all var(--transition-normal);
}

.canCheckIn {
    background: linear-gradient(135deg, #f59e0b, #ef4444);
    color: white;
    box-shadow: 0 4px 14px rgba(245, 158, 11, 0.4);
}

.canCheckIn:hover {
    box-shadow: 0 6px 20px rgba(245, 158, 11, 0.5);
}

.checkedIn {
    background: rgba(16, 185, 129, 0.15);
    color: #10b981;
    cursor: default;
}

/* Compact */
.compact {
    display: inline-flex;
    align-items: center;
    gap: var(--space-1);
    padding: var(--space-2) var(--space-3);
    background: rgba(245, 158, 11, 0.15);
    border: 1px solid rgba(245, 158, 11, 0.3);
    border-radius: var(--radius-full);
}

.fireEmoji {
    font-size: 1rem;
}

.compactCount {
    font-size: 0.875rem;
    font-weight: 700;
    color: #f59e0b;
}

/* Weekly Streak */
.weeklyStreak {
    display: flex;
    gap: var(--space-2);
    padding: var(--space-4);
    background: var(--bg-card);
    border: 1px solid var(--border-subtle);
    border-radius: var(--radius-lg);
}

.weekDay {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--space-2);
    flex: 1;
}

.weekDayLabel {
    font-size: 0.625rem;
    font-weight: 600;
    color: var(--text-muted);
    text-transform: uppercase;
}

.weekDayDot {
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bg-dark-tertiary);
    border-radius: 50%;
    font-size: 0.875rem;
    transition: all var(--transition-normal);
}

.weekDayDot.active {
    background: rgba(245, 158, 11, 0.2);
    border: 1px solid rgba(245, 158, 11, 0.5);
    box-shadow: 0 0 12px rgba(245, 158, 11, 0.3);
}